.App {
  text-align: center;
}

.to-do-item {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  background-color: #fefa85;
  border: 1px #ffff;
  color: #131b2d;
  margin: 1rem;
  height: 13rem;
  border-radius: 10px;
  margin-left: 2rem;
  margin-right: 2rem;
  width: -webkit-fill-available;
  min-width: fit-content;
}

.to-do-item[data-priority="1"] {
  background-color: #fdb06b;
}

.checkbox-title-container {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  flex-basis: 0;
  /* display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-template-rows: repeat(2, 50%); */
  align-items: center;
}

#checkbox {
  grid-column: 1;
  grid-row: 1;
  justify-self: flex-start;
  display: flex;
  height: 2rem;
  width: 2rem;
  margin: 2rem;
}

/* ADJUST THIS LATER*/
#delete-div {
  justify-self: end;
  grid-column: 6;
  grid-row: 1;
  overflow: hidden;
  display: flex;
  gap: 0.5rem;
  /* background-color: aliceblue; */
}

#delete {
  font-family: elza, sans-serif;
  font-weight: 400;
  font-style: normal;
  border: 1px solid #6889f7;
  border-radius: 19px;
  color: #ffff;
  padding: 9px;
  background-color: #131b2d;
  width: 4rem;
}

.check-title-div {
  display: flex;
  align-items: center;
  gap: 1rem;
}

#delete:hover {
  background-color: #6889f7;
  color: #131b2d;
}

/* .to-do-date {
} */

.to-do-title {
  font-family: elza, sans-serif;
  font-weight: 500;
  font-style: normal;
  padding: 1px;
  display: flex;
  flex-shrink: 1;
  align-content: center;
  font-family: elza, sans-serif;
  font-weight: 500;
  font-style: normal;
  width: fit-content;
  color: #131b2d;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  font-size: 1.7rem;
}

.description-box {
  margin-left: 2rem;
  width: fit-content;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  flex-direction: row;
  resize: horizontal;
}

textarea {
  font-family: elza, sans-serif;
  font-weight: 500;
  font-style: normal;
  padding: 10px;
  width: 30rem;
  line-height: 1.5;
  border-radius: 5px;
  border: 1px solid rgb(0, 0, 0);
  background-color: rgb(255 255 255 / 0);
  resize: horizontal;
}

.to-do-title-input {
  font-family: elza, sans-serif;
  font-weight: 500;
  font-size: 1.5rem;
  background-color: rgb(255 255 255 / 0);
  border: 1px solid #131b2d;
  border-radius: 19px;
  height: 2.5rem;
  padding: 4px;
}

.to-do-date-input {
  font-family: elza, sans-serif;
  font-weight: 400;
  font-style: normal;
  border: 1px solid #131b2d;
  border-radius: 19px;
  color: #131b2d;
  background: rgb(255, 255, 255, 0.5);
}

.to-do-date-input:active {
  background: rgb(255, 255, 255);
}
.to-do-date-input:focus {
  background: rgb(255, 255, 255);
}

.form-container {
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  align-items: center;
}

p {
  margin: 0.5rem;
}

form {
  display: flex;
  justify-content: start;
  justify-items: flex-start;
  font-size: 1.2rem;
  flex-wrap: nowrap;
  flex-direction: column;
  align-items: center;
}

.input-field {
  display: flex;
  height: 2rem;
  width: 30rem;
  border-radius: 10px;
  background-color: #131b2d;
  border: solid 1px #6889f7;
  align-content: center;
  font-family: elza, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffff;
  justify-content: center;
  align-content: center;
}

select {
  display: flex;
  height: 2rem;
  width: 30rem;
  border-radius: 10px;
  background-color: #131b2d;
  border: solid 1px #6889f7;
  align-content: center;
  font-family: elza, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffff;
  justify-content: center;
  align-content: center;
}

.submit {
  justify-content: center;
  align-content: center;
  font-family: elza, sans-serif;
  font-weight: 500;
  font-style: normal;
  border: 1px solid #6889f7;
  border-radius: 19px;
  color: #ffff;
  padding: 9px;
  background-color: #131b2d;
  width: 6rem;
  height: 3rem;
}

.submit:hover {
  background-color: #6889f7;
  color: #131b2d;
}

.select-field {
  /* display: flex; */
  height: 2rem;
  width: 6rem;
  border-radius: 10px;
  background-color: #131b2d;
  border: solid 1px #6889f7;
  align-content: center;
  font-family: elza, sans-serif;
  font-weight: 400;
  font-style: normal;
  color: #ffff;
  align-self: center;
  justify-content: center;
  align-content: center;
}

#completed-task-counter {
  grid-column: 1/3;
  justify-self: start;
  width: 21rem;
  text-align: left;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.svg-icon {
  height: 25px;
  margin-left: 5px;
}

.svg-icon:hover {
  animation: spin 0.6s;
}

footer {
  display: flex;
  flex-direction: row;
  margin-bottom: 2rem;
  margin-top: 2rem;
  color: #6889f7;
  font-size: 1rem;
  align-items: flex-end;
  justify-content: center;
}
